.main-form {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  max-width: 720px;

  padding: 2rem;

  input {
    width: 100%;
    padding: 1rem 2rem;
    border-radius: 36px;
    box-shadow: 1px 2px 5px rgba(0, 0, 0, 0.013);
    color: inherit;
    transition: all 0.2s ease-in-out;
  }
  .main-form__button {
    cursor: pointer;
    margin-top: 2rem;
    border-radius: 36px;
    background: #16a086;
    padding: 1rem 2rem;
    color: #fff;
    width: 100%;
    max-width: 300px;
    transition: all 0.2s ease-in-out;
    &:hover {
      background: darken(#16a086, 5%);
    }
  }
}

@media (prefers-color-scheme: dark) {
  .main-form input {
    background: #36393f;
  }
}

@media (prefers-color-scheme: light) {
  .main-form input {
    background: #fff;
  }
}

@media (prefers-color-scheme: no-preference) {
  .main-form input {
    background: #fff;
  }
}
